import React, { useState, useEffect } from 'react';
import './Navbar.scss';
import { Link } from 'react-router-dom';
import { Container, AppBar, Toolbar, Typography } from '@material-ui/core';
import { motion } from 'framer-motion';
import CameraIcon from '@material-ui/icons/Camera';
import Search from '../reusable/Search';
import CategoriesMenu from '../reusable/CategoriesMenu';
import PopMenu from '../reusable/PopMenu';

const Navbar = ({setSearchTerm, setCollection}) => {

   const [ scrollY, setScrollY ] = useState(0);   

   // Adding "scroll" event to window to toggle search box in appBar
   useEffect(() => {
      window.addEventListener('scroll', () => setScrollY(window.scrollY));

      return () => {
         window.removeEventListener('scroll', () => setScrollY(window.scrollY))
      }
         
   }, [])

   return (
      
      <AppBar position='fixed' className={`appBar ${scrollY > 300 && 'active'}`}>
         <Container>
            <Toolbar className='toolBar'>
               
               <Link className='logo-wrapper' to='/'>
                  <Typography variant='h5' className='logo-name'>Pix</Typography>
                  <motion.div className='logo-icon'
                  animate={{transform: 'rotate(180deg)'}}
                  transition={{repeat: Infinity, type: 'Tween', duration: 1.5,
                  ease: 'linear'}}
                  >
                     <CameraIcon/>
                  </motion.div>
               </Link>
               <div className='searchBox-wrapper'>
                  <Search scrollY={scrollY} animateScroll='true' setSearchTerm={setSearchTerm}/>
               </div>
               
               <div className='buttons-wrapper'>
                 <CategoriesMenu setCollection={setCollection}/>
                 <PopMenu/>
               </div>
            </Toolbar>
         </Container>
         
      </AppBar>
      
  
   )
}

export default Navbar;
