import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Grid, Container, Typography } from '@material-ui/core';
import { motion } from 'framer-motion';

const UserPhotos = ({link, setImg, modalEl}) => {

   const [ photos, setPhotos ] = useState(null);

   useEffect(() => {
      const fetchPhotos = async () => {
         const response = await axios.get(link, {
            headers: {
               Authorization: "Client-ID enJlGjf6NGftgeCT-05BkxfCOTvbpCMecPKUDR2bO8w"
            },
            params : {
               per_page: 30
            }
         })

         setPhotos(response.data)
      }
      fetchPhotos();
      
   }, [link])

   const openPhoto = (photo) => {
      setImg(photo);
      modalEl.current.scrollTo(0, 0)
   }

   const renderPhotos = () => {
      if (photos) {
         return (
            <Container maxWidth='lg'>
               <Typography variant='h6'>Photographer's Photos</Typography>
               <Grid container spacing={3} className='gallery'>
                  <Grid item xs={4} className='column'>
                     {photos.map((photo, index) => {
                        if (index < 10) {
                           return <motion.img src={photo.urls.regular} key={index} 
                           alt={photo.alt_description} onClick={() => openPhoto(photo)}
                           whileHover={{scale: 1.02, opacity: 1}}/>
                        }

                        return null;
                     })}
                  </Grid>
                  <Grid item xs={4} className='column'>
                     {photos.map((photo, index) => {
                        if (index > 10 && index < 20) {
                           return <motion.img src={photo.urls.regular} key={index} 
                           alt={photo.alt_description} onClick={() => openPhoto(photo)}
                           whileHover={{scale: 1.02, opacity: 1}}/>
                        }

                        return null;
                     })}
                  </Grid>
                  <Grid item xs={4} className='column'>
                     {photos.map((photo, index) => {
                        if (index > 20 && index < 30) {
                           return <motion.img src={photo.urls.regular} key={index} 
                           alt={photo.alt_description} onClick={() => openPhoto(photo)}
                           whileHover={{scale: 1.02, opacity: 1}}/>
                        }

                        return null;
                     })}
                  </Grid>
               </Grid>
            </Container>
         )
      }

      return null;
   }

   return (
      <div>
         {renderPhotos()}
      </div>
   )
}

export default UserPhotos
