import React from 'react';
import './Filter.scss';
import { Popover, Button, Typography } from '@material-ui/core';
import { ArrowDropDown } from '@material-ui/icons';


const Filter = ({photos, setColor}) => {

   const [ colorsGroup, setColorsGroup ] = React.useState([]);
   const [ open, setOpen ] = React.useState(false);
   const [ anchorEl, setAnchorEl ] = React.useState(null);

   React.useEffect(() => {
      createColors();
   }, [photos])

   const createColors = () => {

      if (photos) {
         const colors = new Set(photos.map(photo => {
            return photo.color;
         }))

         setColorsGroup([...colors]);
      
      }
   }

   const onButtonClick = e => {
      setOpen(true);
      setAnchorEl(e.currentTarget);
   }

   return (
      <React.Fragment>
         <Button variant='outlined' onClick={(e) => onButtonClick(e)}>
            <Typography variant='subtitle2'>Color</Typography>
            <ArrowDropDown/>
         </Button>
         <Popover className='colors-menu' open={open} anchorEl={anchorEl} onClose={() => setOpen(false)}
         anchorOrigin={{vertical: 'bottom', horizontal: 'left'}}>
            <div className='colors-list' style={{}}>
               <div className='color' onClick={() => setColor('All')}>All</div>
               {colorsGroup && colorsGroup.map((color, index) => {
                  return <div className='color' style={{backgroundColor: color}}  key={index}
                  onClick={() => {setColor(color); setOpen(false)}}/>
               })}
            </div>
         </Popover>
      </React.Fragment>
   )
}

export default Filter;
