import React from 'react';
import './Search.scss';
import { motion, AnimatePresence } from 'framer-motion';
import { useHistory } from 'react-router-dom';

import SearchIcon from '@material-ui/icons/Search';

const Search = ({scrollY, animateScroll, setSearchTerm}) => {

   const [ inputTerm, setInputTerm ] = React.useState(null);
   const history = useHistory();

   const handleSubmit = (e) => {
      e.preventDefault();
      setSearchTerm(inputTerm);
      history.push(`/search/${inputTerm}`)
   }

   return (

      <React.Fragment>
         {animateScroll ? (
            <AnimatePresence>
               {scrollY > 600 && (
                  <motion.div className='searchBox'
                  key="search"
                  initial={{y: -100}}
                  animate={{y: 0}}
                  exit={{y: -100}}
                  >
                     <SearchIcon className='searchIcon' onClick={e => handleSubmit(e)}/>
                     <form onSubmit={e => handleSubmit(e)}>
                        <input type='text' placeholder='Search Image...'
                        onChange={e => setInputTerm(e.target.value)}/>
                     </form>
                  </motion.div>
               )}
            </AnimatePresence>
         ) : (
            <motion.div className='searchBox'
            initial={{scale: 0}}
            animate={{scale: 1}}
            >
               <SearchIcon className='searchIcon' onClick={e => handleSubmit(e)}/>
               <form onSubmit={e => handleSubmit(e)}>
                  <input type='text' placeholder='Search Image...'
                  onChange={e => setInputTerm(e.target.value)}/>
               </form>
            </motion.div>
            )
         }
         
      </React.Fragment>
   )
}

export default Search;
