import React from 'react';
import './Banner.scss';
import Search from '../reusable/Search';
import axios from 'axios';

const Banner = ({setSearchTerm}) => {

   const [ photo, setPhoto ] = React.useState(null);

   React.useEffect(() => {
      const fetchRadnomPhoto = async () => {
         const response = await axios.get('https://api.unsplash.com/photos/random', {
            headers: {
               Authorization: "Client-ID enJlGjf6NGftgeCT-05BkxfCOTvbpCMecPKUDR2bO8w"
            },
            params: {
               query: 'nature',
               orientation: 'landscape'
            }
         })

         setPhoto(response)
      };

      fetchRadnomPhoto();
   }, [])
   
   return (
      <div className='banner' style={photo ? {backgroundImage: `url(${photo.data.urls.regular})`} : null}>
         <div className='overlay'/>
         <Search setSearchTerm={setSearchTerm}/>
      </div>
   )
}

export default Banner;
