import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import { Popover, MenuList, MenuItem, Typography } from '@material-ui/core'; 
import { ArrowDropDown } from '@material-ui/icons';

const CategoriesMenu = ({setCollection}) => {

   const [ categories, setCategories ] = useState(null);
   const [ open, setOpen ] = useState(false);
   const [ anchorEl, setAnchorEl ] = useState(null);

   useEffect(() => {
      const fetchCategories = async () => {
         const response = await axios.get('https://api.unsplash.com/collections', {
            headers: {
               Authorization: "Client-ID enJlGjf6NGftgeCT-05BkxfCOTvbpCMecPKUDR2bO8w"
            },
            params: {
               per_page: 30
            }
         })
         
         setCategories(response.data);   
      }

      fetchCategories();
   }, [])

   const renderItems = () => {
      if (categories) {
         return categories.map(category => {
            return (
            
            <MenuItem key={category.id} onClick={() => setCollection(category)}>
               <Link to={`/collection/${category.title}/${category.id}`}
               style={{color: 'black', textDecoration: 'none'}}>
                  {category.title}
               </Link>
            </MenuItem>
            
            )
         })
      }
   }

   const handleMenuToggle = e => {
      setOpen(true);
      setAnchorEl(e.currentTarget)
   }

   return (
      <React.Fragment>
         <div className='categories' onClick={(e) => handleMenuToggle(e)}>
            <Typography variant='subtitle2'>Categories</Typography>
            <ArrowDropDown/>
         </div>
         <Popover open={open} anchorEl={anchorEl} onClose={() => setOpen(false)}
         anchorOrigin={{vertical: 'bottom', horizontal: 'left'}}
         style={{maxHeight: '400px'}}>
            <MenuList>
               {renderItems()}
            </MenuList>
         </Popover>
      </React.Fragment>
      
   )
}

export default CategoriesMenu;
