import React, { useState, useEffect } from 'react';
import './Gallery.scss';
import axios from 'axios';
import { Grid, Container } from '@material-ui/core';
import { Pagination } from '@material-ui/lab';
import PhotoModal from './modal/PhotoModal';
import { motion } from 'framer-motion';


import Filter from '../reusable/Filter';

const GallerySearch = ({searchTerm}) => {

   const [ photos, setPhotos ] = useState(null); // Set List of returned photos
   const [ page, setPage ] = useState(1); // Set page of pagination
   const [ openModal, setOpenModal ] = useState(false); // Open and Close Modal
   const [ img, setImg ] = useState(null); // Set Image passed to Modal
   const [ color, setColor ] = useState('All'); // Filter by color

   useEffect(() => {
      
      const fetchPhotos = async () => {
         
         const response = await axios.get('https://api.unsplash.com/search/photos', {
            headers: {
               Authorization: "Client-ID enJlGjf6NGftgeCT-05BkxfCOTvbpCMecPKUDR2bO8w"
            }, params: {
               query: searchTerm,
               per_page: 30,
               page
            }
         });
         
         setPhotos(response.data.results)
      }

      {searchTerm && fetchPhotos()}

   }, [searchTerm, page])

   const openPhoto = (photo) => {
      setOpenModal(true);
      setImg(photo);
   }

   const renderPhotos = () => {
      if (photos) {
         
         return (
            <div className='gallery-search' style={{padding: '15px 0'}}>
               <Container>
                  <Filter photos={photos} setColor={setColor}/>
                  <Grid container spacing={3} className='gallery'>
                     <Grid item xs={4} className='column'>
                        {photos.map((photo, index) => {
                           if (index < 10) {
                              return <motion.img src={photo.urls.regular} key={index} 
                              alt={photo.alt_description} onClick={() => openPhoto(photo)}
                              whileHover={{scale: 1.02, opacity: 1}}/>
                           }

                           return null;
                        })}
                     </Grid>
                     <Grid item xs={4} className='column'>
                        {photos.map((photo, index) => {
                           if (index > 10 && index < 20) {
                              return <motion.img src={photo.urls.regular} key={index} 
                              alt={photo.alt_description} onClick={() => openPhoto(photo)}
                              whileHover={{scale: 1.02, opacity: 1}}/>
                           }

                           return null;
                        })}
                     </Grid>
                     <Grid item xs={4} className='column'>
                        {photos.map((photo, index) => {
                           if (index > 20 && index < 30) {
                              return <motion.img src={photo.urls.regular} key={index} 
                              alt={photo.alt_description} onClick={() => openPhoto(photo)}
                              whileHover={{scale: 1.02, opacity: 1}}/>
                           }

                           return null;
                        })}
                     </Grid>
                  </Grid>
                  <Pagination count={10} page={page} variant="outlined" shape="rounded" className='pagination'
                  onChange={(e, pageNum) => setPage(pageNum)}/>
                  <PhotoModal img={img} open={openModal} setOpen={setOpenModal} setImg={setImg}/>
               </Container>
            </div>
         )
      }

      return 'loading'
   }

   const renderFilteredPhotos = () => {
      return (
         <div style={{padding: '15px 0'}}>
            <Container>
               <Filter photos={photos} setColor={setColor}/>
               <Grid container spacing={3} className='gallery'>
                  {photos.map((photo, index) => {
                     if (photo.color === color) {
                        return (
                           <Grid item xs={4} className='column'>
                              <motion.img src={photo.urls.regular} key={index} 
                              alt={photo.alt_description} onClick={() => openPhoto(photo)}
                              whileHover={{scale: 1.02, opacity: 1}}/>
                           </Grid>
                        )
                     }

                     return null;
                  })}
                  
               </Grid>
            </Container>
         </div>
      )
      
   }

   return color === 'All' ? renderPhotos() : renderFilteredPhotos();
}

export default GallerySearch;
