import React from 'react';
import { HashRouter, Switch, Route } from 'react-router-dom';
import CollectionPage from './components/pages/CollectionPage';
import HomePage from './components/pages/HomePage';
import SearchPage from './components/pages/SearchPage';
import Banner from './components/layout/Banner';
import Navbar from './components/layout/Navbar';

const App = () => {

   const [ searchTerm, setSearchTerm ] = React.useState(null);
   const [ collection, setCollection ] = React.useState(null);


   return (
      <React.Fragment>
         
         <HashRouter>
            <Navbar setSearchTerm={setSearchTerm} setCollection={setCollection}/>
            <Banner setSearchTerm={setSearchTerm}/>
            <Switch>
               <Route path='/' exact render={({...props}) => {
                  return <HomePage {...props}/>
               }}/>
                  
               <Route path='/search/:term' render={({...props}) => {
                  return <SearchPage {...props} searchTerm={searchTerm} 
                  setSearchTerm={setSearchTerm}/>
               }}/>

               <Route path='/collection/:category/:id' render={({...props}) => {
                  return <CollectionPage {...props} collection={collection} 
                  setCollection={setCollection}/>
               }}/> 

            </Switch>
         </HashRouter>

         
      </React.Fragment>
   )
}

export default App;
