import React, { useEffect, useRef } from 'react';
import ReactDOM from 'react-dom';
import './PhotoModal.scss';
import { motion } from 'framer-motion';
import CloseIcon from '@material-ui/icons/Close';
import { Typography, Button } from '@material-ui/core';
import UserPhotos from './UserPhotos';
 
function PhotoModal({img, open, setOpen, setImg}) {

   const modalEl = useRef(null)

   useEffect(() => {
      // Show and Hide Scroll of the body 
      if (open) {
         document.body.classList.add('overflow-hidden')
      } else {
         document.body.classList.remove('overflow-hidden')
      }   
   })

   // Creating protal and appedning modal into it
   if (open && img) {
      return ReactDOM.createPortal(
         <motion.div className='modal-wrapper' onClick={() => setOpen(false)} 
         ref={modalEl}
         key="photoModalKey"
         initial={{y: '200px'}}
         animate={{y: 0}}
         exit={{y: '200px'}}
         >
            <div className='modal-close'>
               <CloseIcon style={{color: 'white'}}/>
            </div>
            <div className='modal-body' onClick={e => e.stopPropagation()}>
               <div className='modal-header' >
                  <div className='photographer'>
                     <a href={img.user.links.html} target='_blank' rel="noreferrer">
                        <img src={img.user.profile_image.small} alt='user'/>
                        <Typography variant='caption'>{img.user.name}</Typography>
                     </a>
                  </div>
                  <div className='download'>
                     <Button variant='outlined'>
                        <a href={`${img.links.download}?force=true`} rel="noreferrer">Download</a>
                     </Button>
                  </div>
               </div>
               <div className='modal-image'>
                  <img src={img.urls.regular} alt={img.alt_description}/>
               </div>
               <div className='image-description'>
                  <Typography>{img.alt_description}</Typography>
               </div>
               <UserPhotos link={img.user.links.photos} setImg={setImg} modalEl={modalEl}/>
            </div>
         </motion.div>,
         document.getElementById('photo-modal'))
   }
   
   return null;
   
}

export default PhotoModal;
