import React from 'react';
import axios from 'axios';
import { useParams } from 'react-router-dom';
import CollectionGallery from '../layout/CollectionGallery';

const CollectionPage = ({collection, setCollection}) => {

   const params = useParams();

   React.useEffect(() => {
      if (!collection) {

         const collectionID = params.id;
   
         const fetchCollection = async () => {
            const response = await axios.get(`https://api.unsplash.com/collections/${collectionID}`, {
               headers: {
                  Authorization: "Client-ID enJlGjf6NGftgeCT-05BkxfCOTvbpCMecPKUDR2bO8w"
               }
            })

            setCollection(response.data);
         }

         fetchCollection();
      }
   })
   
   return <CollectionGallery collection={collection}/>
   
}

export default CollectionPage;
