import React from 'react';
import GallerySearch from '../layout/GallerySearch';


const SearchPage = (props) => {

   React.useEffect(() => {
      if (!props.searchTerm) {
         props.setSearchTerm(props.match.params.term)
      }
    })

   return <GallerySearch searchTerm={props.searchTerm}/>
      
}

export default SearchPage;
